
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: grid;
  /* justify-content: center; */
  text-align: center;
  /* padding: 10px; */
  width: 100%;
}

.wrapper{
 text-align: center;
 width: 100%;
 padding: 0 auto;
 background-color: rgb(232, 242, 243);
}

h2 , h4{ 
  text-align: center;
  text-transform: capitalize;
  padding: 10px 0 30px;
  max-width: 100%;
}
.wrapper > h4 {
  padding-bottom: 80px !important;
}

.head{
  margin: 30px 10px 80px;
  text-align: center;
}

.card {
  display: flex;
  flex-direction: column;
  width: 90%;
  justify-content: center;
  margin: 5px auto ;
  max-width: 100%;
  /* border: 1px solid red; */
}

.card1{  
  display: grid;
  justify-content: center;
  text-align: center !important;
  padding: 2px auto;
  margin: 5px auto 50px ;
  background-color: rgb(238, 238, 224);
  box-shadow:  1px 2px 3px 3px solid rgb(41, 40, 40), 
               1px 2px 3px 3px solid rgb(119, 114, 114) ;
  border-bottom: 1px solid gray;
}

.card1 #id{
  padding-left: 20px;
  font-weight: bolder;
}

.card1 #id1 {
  display: flex;
  justify-content: center;
  font-size: 1rem;
  color: rgb(238, 40, 40);
  padding-bottom: 5px;
}

.card1 #img{
  width: 200px;
  max-width: 100%;
  margin:  0 auto;
}


.card1 #titolo { 
  font-size: .7rem;
  font-weight: bolder;
}

.card1 #descrizione{
  font-size: .8rem;
  display: flex;
  flex-wrap: wrap;
  font-weight: lighter ;
  font-weight: bolder;
  padding: 10px 25px 30px;
}


.card1 #prezzo {
  margin-bottom: 20px;
 font-weight: bolder;
}


.card1  #bottoni {
    margin: 2px 0 10px !important; 
}

.card1 #carrello 
, .card1 #idProdotto {
  background-color: rgb(215, 238, 230);
  width: 140px;
  font-size: .7rem;
  font-weight: bolder;
  padding:  7px 15px !important;
  margin: 10px ;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  color: rgb(20, 20, 20);
  margin-bottom: 15px;
  transition: 1s all ease-in-out;
  -webkit-transition: 1s all ease-in-out;
  -moz-transition: 1s all ease-in-out;
  -ms-transition: 1s all ease-in-out;
  -o-transition: 1s all ease-in-out;
}
.card1 #carrello:hover 
, .card1 #idProdotto:hover {
    cursor: pointer;
    border: 2px solid rgb(147, 236, 147);
    background-color: rgb(64, 131, 231);
}

/*   end Card  */



  /****        ****/ 

/*   SPAZIO  */
.space  {
  margin: 10px 0;
  word-wrap: break-word;
}


#uno{
  padding: 0 20px;
}


/*  */
.spaceOne  {
  margin: 20px 0 ;
  word-break: break-word;
}






@media only screen and (max-width:550px){ 

code {
   font-size: 1.3vw;

}
 

.card1 > p {
  font-size: 2.5vw;
  display: flex;
  
}


pre{
  font-size: 1.5vw !important;
}

}